

/* eslint-disable no-unused-vars */
import { useCallback, useContext, useEffect } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import BreadcrumbCom from "../BreadcrumbCom";
import ProductCardStyleOne from "../Helpers/Cards/ProductCardStyleOne";
import Layout from "../Partials/Layout";
import ProductsFilter from "./ProductsFilter";
import { Loader } from "lucide-react";
import SEOHeader from "../Partials/Headers/HeaderOne/SEOHeader";
import Pagination from "./Pagination";
import { PromoProductContext } from "../../Provider/PromoProductContext";

export default function PromoProductPage() {
  const {
    currentProductsPromo,
    totalProductsPromo,
    isLoadingProductPromo,
    fetchProdcutsPromo,     // ⚠️ appelé à chaque changement de `filters`
    updateFilters,
    filters,
    resetFilters,
  } = useContext(PromoProductContext);

  const location = useLocation();
  const navigate = useNavigate();

  // 1) Lecture des query params → contexte (inclut tag & productType)
  useEffect(() => {
    resetFilters();
    const params = new URLSearchParams(location.search);

    const newFilters = {
      page: Number(params.get("page")) || 1,
      category: params.get("category") || "All",
      search: params.get("search") || "",
      min: Number(params.get("min")) || 1000,
      max: Number(params.get("max")) || 5000000,
      limit: Number(params.get("limit")) || 9,
      productType: params.get("productType") || "All", // ← ajouté
      tag: params.get("tag") || "All",                 // ← ajouté
    };

    updateFilters(newFilters);

    window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [location.search, location.pathname]);

  // 2) Re-fetch quand les filtres changent
  useEffect(() => {
    fetchProdcutsPromo();
  }, [fetchProdcutsPromo, filters]);

  // 3) Helpers URL
  const updateUrlParams = useCallback(
    (nextFilters) => {
      const params = new URLSearchParams(location.search);

      Object.entries(nextFilters).forEach(([key, value]) => {
        // On enlève de l’URL les valeurs “neutres”
        const isNeutral =
          value === undefined ||
          value === null ||
          value === "" ||
          value === "All";

        if (isNeutral) {
          params.delete(key);
        } else {
          params.set(key, String(value));
        }
      });

      navigate({ search: params.toString() }, { replace: true });
    },
    [location.search, navigate]
  );

  // 4) Handlers
  const handlePageChange = (pageNumber) => {
    updateUrlParams({ ...filters, page: pageNumber });
    window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
  };

  const handleSearchChange = (e) => {
    const value = e.target.value;
    updateUrlParams({ ...filters, search: value, page: 1 });
    window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
  };

  const handleCategoryChange = (categoryName) => {
    updateUrlParams({ ...filters, category: categoryName || "All", page: 1 });
    window.scrollTo({ top: 0, left: 0, behavior: "smooth" });
  };

  // 5) Affichage
  const startIndex =
    totalProductsPromo > 0 ? (filters.page - 1) * filters.limit + 1 : 0;
  const endIndex = Math.min(filters.page * filters.limit, totalProductsPromo);

  return (
    <>
      <SEOHeader
        title="CCBM Shop | En Promo"
        description="Découvrez les meilleures offres promotionnelles sur CCBM Shop, votre destination privilégiée pour l'électroménager de qualité à prix réduits."
        keywords="électroménager, promotions, offres spéciales, CCBM Shop, ccbme, appareils électroménagers à prix réduits"
      />
      <Layout>
        <div className="products-page-wrapper w-full">
          <div className="container-x mx-auto">
            <BreadcrumbCom />
            <div className="w-full lg:flex lg:space-x-[30px]">
              {/* Filtres */}
              <div className="lg:w-[270px]">
                <ProductsFilter
                  updateUrlParams={updateUrlParams}
                  handleCategoryChange={handleCategoryChange}
                />
              </div>

              {/* Grille produits */}
              <div className="flex-1">
                <div className="products-sorting w-full bg-white flex flex-col md:flex-row justify-between p-[30px] mb-[40px]">
                  <p className="font-400 text-[13px]">
                    <span className="text-qgray">Affichage</span>{" "}
                    {totalProductsPromo > 0
                      ? `${startIndex}–${endIndex} sur ${totalProductsPromo} résultats`
                      : "0 résultat"}
                  </p>

                  {/* Si tu veux réactiver la recherche texte */}
                  {/* <input
                    type="search"
                    placeholder="Rechercher des produits en promo"
                    value={filters.search}
                    onChange={handleSearchChange}
                    className="px-3 py-2 border border-gray-300 rounded-md"
                  /> */}
                </div>

                {isLoadingProductPromo ? (
                  <div className="flex justify-center items-center">
                    <Loader className="animate-spin" /> Chargement
                  </div>
                ) : currentProductsPromo.length === 0 ? (
                  <div className="flex justify-center items-center">
                    Aucun produit en promotion trouvé
                  </div>
                ) : (
                  <div className="grid xl:grid-cols-3 grid-cols-2 gap-[30px]">
                    {currentProductsPromo.map((product) => (
                      <ProductCardStyleOne key={product.id} datas={product} />
                    ))}
                  </div>
                )}

                <Pagination
                  currentPage={filters.page}
                  totalPages={Math.ceil(totalProductsPromo / filters.limit)}
                  onPageChange={handlePageChange}
                />
              </div>
            </div>
          </div>
        </div>
      </Layout>
    </>
  );
}
