

/* eslint-disable no-unused-vars */
/* eslint-disable react/prop-types */
import { useContext, useEffect, useState } from "react";
import RangeSlider from "react-range-slider-input";
import { PromoProductContext } from "../../Provider/PromoProductContext";
import Categorieservice from "../../services/CategorieService";

export default function ProductsFilter({ className, filterToggle, updateUrlParams }) {
  const { filters, updateFilters } = useContext(PromoProductContext);
  const [tags, setTags] = useState([]);

  // Charger les tags
  useEffect(() => {
    const fetchTags = async () => {
      try {
        const data = await Categorieservice.getTags();
        // data attendu: [{ id, name, slug }, ...]
        setTags(Array.isArray(data) ? data : []);
      } catch (error) {
        console.error("Erreur lors de la récupération des tags", error);
        setTags([]);
      }
    };
    fetchTags();
  }, []);

  const handlePriceChange = (value) => {
    const [min, max] = value;
    const next = { ...filters, min, max, page: 1 };
    updateFilters(next);
    updateUrlParams(next);
  };

  const handleTagChange = (e) => {
    const selected = e.target.value; // "All" ou un slug
    const next = { ...filters, tag: selected, page: 1 };
    updateFilters(next);
    updateUrlParams(next);
  };

  const handleTypeFilterChange = (e) => {
    const newType = e.target.value; // "All" | "..." 
    const next = { ...filters, productType: newType, page: 1 };
    updateFilters(next);
    updateUrlParams(next);
  };

  return (
    <div
      className={`filter-widget w-full fixed lg:relative left-0 top-0 h-screen z-10 lg:h-auto overflow-y-scroll lg:overflow-y-auto bg-white px-[30px] pt-[40px] ${
        className || ""
      } ${filterToggle ? "block" : "hidden lg:block"}`}
    >

      {/* Tags */}
      <div className="filter-subject-item pb-10 border-b border-qgray-border mt-10">
        <div className="subject-title mb-[20px]">
          <h1 className="text-black text-base font-500">Filtrer par tags</h1>
        </div>
        <div className="text-sm text-qgray">
          <select
            className="w-full border border-qgray-border px-3 py-2 rounded"
            value={filters.tag ?? "All"}
            onChange={handleTagChange}
          >
            <option value="All">Tous les tags</option>
            {tags.map((tag) => (
              <option key={tag.id} value={tag.slug || tag.name}>
                {tag.name}
              </option>
            ))}
          </select>
        </div>
      </div>

      {/* Prix */}
      <div className="filter-subject-item pb-10 border-b border-qgray-border mt-10">
        <div className="subject-title mb-[30px]">
          <h1 className="text-black text-base font-500">Échelle des prix</h1>
        </div>
        <div className="price-range mb-5">
          <RangeSlider
            value={[filters.min, filters.max]}
            onInput={handlePriceChange}
            min={1000}
            max={5000000}
          />
        </div>
        <p className="text-xs text-qblack font-400">
          Prix (FCFA): {filters.min} - {filters.max}
        </p>
      </div>
    </div>
  );
}
